:root {
  --font-alata:"Alata", sans-serif;
  --font-alata-weight:400;
  --font-josefin:"Josefin Sans", sans-serif;
  --font-josefin-weight:300;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-gray:hsl(0, 0%, 55%);
  --very-dark-gray: hsl(0, 0%, 41%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex__jc-sb {
  justify-content: space-between;
}
.flex__jc-c {
  justify-content: center;
}
.flex__jc-ai {
  align-items: center;
}
.flex__jc-end {
  justify-content: flex-end;
}

.grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
.grid__ji-c {
  justify-items: center;
}
.grid__ai-c {
  align-items: center;
}
@media (min-width: 71.875em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.heading {
  color: black;
  text-transform: uppercase;
  font-family: var(--font-josefin);
  letter-spacing: 0.3125rem;
  padding-top: 3.75rem;
  font-size: 2rem;
}
@media (min-width: 71.875em) {
  .heading {
    letter-spacing: 0.5rem;
    padding-top: 1.25rem;
    font-size: 2.5rem;
  }
}

.para {
  color: var(--very-dark-gray);
  font-family: var(--font-alata);
  font-size: 0.875rem;
  padding-top: 2.375rem;
}
@media (min-width: 71.875em) {
  .para {
    font-size: 1rem;
    padding-top: 2.8125rem;
  }
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none;
  }
}

@media (max-width: 39.9375em) {
  .posters {
    background-repeat: no-repeat;
    position: relative;
    background-size: cover;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    height: 20vh;
  }
}
@media (min-width: 71.875em) {
  .posters {
    background-repeat: no-repeat;
    position: relative;
    background-size: cover;
    display: flex;
    align-items: flex-end;
    height: 60vh;
  }
}

.links {
  color: white;
  font-family: var(--font-alata);
  font-size: 0.875rem;
  padding-right: 1.875rem;
}
@media (max-width: 39.9375em) {
  .links {
    color: white;
    font-family: var(--font-alata);
    font-size: 0.875rem;
    padding-top: 2.8125rem;
    display: block;
  }
}

.pr {
  padding-right: 0.9375rem;
}

.pr2 {
  padding-right: 1.25rem;
}

.copyright {
  color: var(--very-dark-gray);
  font-family: var(--font-alata);
}

@media (max-width: 39.9375em) {
  .category {
    color: white;
    margin-left: 2.625rem;
    font-family: var(--font-josefin);
    font-size: 1.875rem;
    margin-top: 9.375rem;
    position: absolute;
  }
}
@media (min-width: 71.875em) {
  .category {
    color: white;
    margin-left: 2.625rem;
    font-family: var(--font-josefin);
    font-size: 1.875rem;
    margin-top: 9.375rem;
    position: absolute;
  }
}

@media (max-width: 71.8125em) {
  .top {
    background-image: url("../../../images/mobile/image-hero.jpg");
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
  }
  .top__nav {
    display: flex;
    padding-top: 2.1875rem;
    justify-content: space-between;
  }
  .top__nav--hamburger {
    padding-right: 1.5625rem;
  }
  .top__nav--logo {
    padding-left: 1.25rem;
  }
  .top__headingContainer {
    width: 18.75rem;
    height: 15.625rem;
    transform: translate(10%, 50%);
    border: 1px solid white;
  }
  .top__headingContainer--text {
    font-family: var(--font-josefin);
    font-size: 2rem;
    color: white;
    padding-left: 0.625rem;
  }
}
@media (min-width: 71.875em) {
  .top {
    background-image: url("../../../images/desktop/image-hero.jpg");
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
  }
  .top__nav {
    display: flex;
    justify-content: space-between;
    padding-top: 3.4375rem;
  }
  .top__nav--logo {
    padding-left: 3.75rem;
  }
  .top__nav--text {
    text-transform: uppercase;
    padding-right: 3.125rem;
  }
  .top__nav--text a {
    font-size: 1rem;
    letter-spacing: 0.125rem;
    color: white;
    margin-right: 2.5rem;
    font-family: var(--font-alata);
  }
  .top__headingContainer {
    overflow: hidden;
    display: grid;
    align-items: center;
    position: relative;
    border: 1px solid white;
    margin-left: 10rem;
    width: 31.25rem;
    font-size: 3.125rem;
    height: 15.625rem;
    margin-top: 15.625rem;
  }
  .top__headingContainer--text {
    letter-spacing: 0.3125rem;
    line-height: 0.1;
    margin-left: 1.25rem;
    position: absolute;
    font-family: var(--font-josefin);
    font-weight: var(--font-josefin-weight);
    color: white;
  }
}

@media (max-width: 39.9375em) {
  .hero {
    height: 100vh;
    text-align: center;
  }
  .hero__image {
    padding-top: 5.625rem;
    width: 100%;
  }
  .hero__image img {
    width: 80%;
  }
  .hero__text .para {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }
}
@media (min-width: 71.875em) {
  .hero {
    height: 100vh;
  }
  .hero__wrapper {
    position: relative;
  }
  .hero__image {
    padding-top: 10rem;
    text-align: center;
  }
  .hero__image img {
    vertical-align: top;
    transform: translateX(-25%);
  }
  .hero__text {
    position: absolute;
    background-color: white;
    bottom: 0;
    width: 43.75rem;
    padding-left: 6.25rem;
    right: 0;
  }
}

@media (max-width: 39.9375em) {
  .main {
    text-align: center;
  }
  .main__heading {
    font-size: 2.375rem;
    text-align: center;
    font-family: var(--font-josefin);
    margin-top: 0.625rem;
  }
  .main__btn {
    margin-top: 1.25rem;
  }
  .main__btn button {
    border-style: none;
    border: 2px solid black;
    color: black;
    transition: 500ms ease-in-out;
    width: 9.375rem;
    font-size: 1rem;
    height: 2.8125rem;
    background-color: white;
    font-family: var(--font-josefin);
  }
  .main__grid {
    display: grid;
    padding: 0 1.25rem;
    gap: 0.625rem;
    grid-template-rows: repeat(8, 1fr);
  }
  .main__grid--one {
    background-image: linear-gradient(to right, black, rgba(255, 255, 255, 0)), url("../../../images/mobile/image-deep-earth.jpg");
  }
  .main__grid--two {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-night-arcade.jpg");
  }
  .main__grid--three {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-soccer-team.jpg");
  }
  .main__grid--four {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-grid.jpg");
  }
  .main__grid--five {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-from-above.jpg");
  }
  .main__grid--six {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-pocket-borealis.jpg");
  }
  .main__grid--seven {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-curiosity.jpg");
  }
  .main__grid--eight {
    background-image: linear-gradient(to top right, rgba(0, 0, 0, 0.623), rgba(255, 255, 255, 0)), url("../../../images/mobile/image-fisheye.jpg");
  }
}
@media (min-width: 71.875em) {
  .main__top {
    display: flex;
    padding-left: 10rem;
    padding-right: 10rem;
    padding-top: 0.625rem;
    justify-content: space-between;
    align-items: center;
  }
  .main__top--heading {
    font-family: var(--font-josefin);
    font-size: 2.625rem;
  }
  .main__top--btn button {
    border-style: none;
    border: 2px solid black;
    color: black;
    transition: 500ms ease-in-out;
    width: 10rem;
    font-size: 1rem;
    height: 2.8125rem;
    background-color: white;
    font-family: var(--font-josefin);
  }
  .main__top--btn button:hover {
    background-color: black;
    color: white;
    box-shadow: 0 8px 8px -4px black;
    border: 2px solid white;
  }
  .main__grid {
    display: grid;
    padding-left: 10rem;
    gap: 1.25rem;
    padding-right: 10rem;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
  .main__grid--one {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-deep-earth.jpg");
  }
  .main__grid--two {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-night-arcade.jpg");
  }
  .main__grid--three {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-soccer-team.jpg");
  }
  .main__grid--four {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-grid.jpg");
  }
  .main__grid--five {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-from-above.jpg");
  }
  .main__grid--six {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-pocket-borealis.jpg");
  }
  .main__grid--seven {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-curiosity.jpg");
  }
  .main__grid--eight {
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.61)), url("../../../images/desktop/image-fisheye.jpg");
  }
}

.footer {
  margin-top: 7.5rem;
  width: 100%;
  background-color: black;
}
@media (max-width: 39.9375em) {
  .footer {
    text-align: center;
  }
  .footer img {
    margin-top: 3.75rem;
  }
  .footer__socials {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer p {
    color: var(--dark-gray);
    vertical-align: bottom;
    font-family: var(--font-alata);
    display: inline-block;
    padding-bottom: 4.375rem;
    padding-top: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .footer__socials {
    display: flex;
    padding: 2.8125rem 5.625rem;
    justify-content: space-between;
  }
  .footer__copyright {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5.625rem 1.875rem 5.625rem;
  }
}